$color-primary: rgba(255, 255, 255, .99);
$color-secondary: #F3976C;
$color-dark: #1C1C1C;

body {
    margin: 0;
    padding: 0;
    background: $color-primary;
    overflow-x: hidden;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: $color-primary; }


a,
button {
    transition: all .3s ease-in-out;
    outline: none!important;
    color: $color-dark; }


a:hover, a:focus-visible {
    text-decoration: none;
    cursor: pointer; }


#page-loading-blocs-notifaction {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 100%;
    z-index: 100000;
    background: $color-primary url("../img/pageload-spinner.gif") no-repeat center center; }


.bloc {
    width: 100%;
    clear: both;
    background: 50% 50% no-repeat;
    padding: 0 50px;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    position: relative; }

.bloc .container {
    padding-left: 0;
    padding-right: 0; }


.bloc-lg {
    padding: 100px 50px; }


.bloc-md {
    padding: 50px; }


.bloc-sm {
    padding: 20px 50px; }

.bg-repeat {
    background: repeat; }


.texture-paper::before {
    background: url("../img/texture-paper.png");
    background-size: 280px 280px;
    opacity: 2; }

.banniere-texture::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0; }

.b-parallax {
    background-attachment: fixed !important; }


.d-bloc {
    color: rgba(255, 255, 255, .7); }


.d-bloc button:hover {
    color: rgba(255, 255, 255, .9); }


.footer {
    background: $color-secondary;
    color: rgba(255, 255, 255, .8); }

.footer li:hover, .footer li:focus-visible {
    color: $color-dark;
    text-decoration: none; }

.voffset {
    margin-top: 30px; }


.voffset-lg {
    margin-top: 80px; }


.row-no-gutters {
    margin-right: 0;
    margin-left: 0; }


.row.row-no-gutters > [class^="col-"],
.row.row-no-gutters > [class*=" col-"] {
    padding-right: 0;
    padding-left: 0; }

.banniere-img {
    background-image: url("../img/la-chouette-agence-banniere.jpg");
    background-repeat: no-repeat;
    background-size: auto !important; }

.presentation {
    background-position-y: top; }

.objectifs {
    background-image: url("../img/image-de-presentation.bmp"); }

.header__nav {
    margin-bottom: 0;
    z-index: 1; }

header a:focus {
    outline: grey thin; }

.header__nav:before, .header__nav:after {
    content: '';
    height: 1px;
    background: #cccccc;
    width: 65px;
    margin-bottom: 3px;
    margin-top: 3px; }

.header__nav--logo {
    height: auto;
    padding: 3px 15px;
    font-size: 25px;
    font-weight: normal;
    font-weight: 600;
    line-height: 44px; }


.header__nav--logo img {
    max-height: 200px;
    margin: 0 5px 0 0;
    display: inline;
    min-width: 100px;
    height: 40px; }


.nav-center .header__nav--logo img {
    margin: 0; }


.header__nav .header__nav--menu {
    float: right;
    z-index: 1; }


.header__nav--menu > li {
    float: left;
    margin-top: 4px;
    font-size: 16px; }


.open .dropdown-menu > li > a {
    text-align: inherit; }


.header__nav--menu > li a:hover,
.header__nav--menu > li a:focus-visible {
    background: transparent; }


.navbar-toggle {
    margin: 10px 10px 0 0;
    border: 0px; }


.navbar-toggle:hover {
    background: transparent!important; }


.navbar-toggle .icon-bar {
    background-color: rgba(0, 0, 0, .5);
    width: 26px; }


.nav-invert .header__nav .header__nav--menu {
    float: left; }


.nav-invert ,
.nav-invert .header__nav--logo {
    float: right;
    position: relative;
    z-index: 2; }


@media (min-width: 768px) {
    .header__nav--menu {}


    .header__nav--menu > li .dropdown-menu a,
    .header__nav--menu > li .dropdown-menu a:hover,
    .header__nav--menu > li .dropdown-menu a:focus-visible {
        color: #484848; }

    .nav-invert .header__nav--menu {
        left: 0;
        right: 0; }

    .nav-center {
        text-align: center; }

    .nav-center {
        width: 100%; }

    .nav-center ,
    .nav-center .header__nav--logo,
    .nav-center .header__nav--menu > li {
        float: none;
        display: inline-block; }

    .nav-center .header__nav--menu {
        position: relative;
        width: 100%;
        right: 0;
        margin-right: 0px;
        margin-top: 20px; }

    .nav-center.mini-nav .navbar-toggle {
        float: none;
        margin: 10px auto 0; } }



.header__nav--menu > li > .dropdown a {
    background: none!important;
    display: block;
    padding: 14px 15px; }


.header__nav .caret {
    margin: 0 5px; }


.dropdown-menu .dropdown-menu {
    top: -8px;
    left: 100%; }


.dropdown-menu .dropmenu-flow-right {
    top: 100%;
    left: 0;
    margin-left: -1px;
    border-top-left-radius: 0;
    border-top-right-radius: 0; }


.dropdown-menu .dropdown span {
    border: 4px solid black;
    border-top-color: transparent;
    border-right-color: transparent;
    border-bottom-color: transparent;
    margin: 6px -5px 0 0!important;
    float: right; }


.mg-md {
    margin-top: 10px;
    margin-bottom: 20px; }


.mg-lg {
    margin-top: 10px;
    margin-bottom: 40px; }


.btn {
    margin: 0 5px 5px 0; }


.btn.pull-right {
    margin: 0 0 5px 5px; }


button {
    outline: none!important; }


.btn-radius {
    border-radius: 40px; }


.btn-clean {
    border: 1px solid rgba(0, 0, 0, .08);
    border-bottom-color: rgba(0, 0, 0, .1);
    text-shadow: 0 1px 0 rgba(0, 0, 1, .1);
    box-shadow: 0 1px 3px rgba(0, 0, 1, .25), inset 0 1px 0 0 rgba(255, 255, 255, .15); }

.presentation__logo {
    width: 100px; }

.services__top--text {
    padding: 0px 193px;
    font-size: 22px;
    color:  rgba(0, 0, 0, 0.61);
    margin: 10px 0; }

.services__mid--text {
    color: $color-dark; }
.icon-md {
    font-size: 30px!important; }

.services__bot--text {
    padding: 0px 55px;
    font-size: 20px;
    color:  rgba(0, 0, 0, 0.61);
    font-style: italic; }

.services__bot--nom {
    font-size: 15px; }

.icon-lg {
    font-size: 60px!important; }


.sm-shadow {
    text-shadow: 0 1px 2px rgba(0, 0, 0, .3); }


.panel-sq,
.panel-sq .panel-heading,
.panel-sq .panel-footer {
    border-radius: 0; }


.panel-rd {
    border-radius: 30px; }


.panel-rd .panel-heading {
    border-radius: 29px 29px 0 0; }


.panel-rd .panel-footer {
    border-radius: 0 0 29px 29px; }


.form-control {
    border-color: rgba(0, 0, 0, .1);
    box-shadow: none; }


.scrollToTop {
    width: 40px;
    height: 40px;
    position: fixed;
    bottom: 20px;
    right: 20px;
    opacity: 0;
    z-index: 500;
    transition: all .3s ease-in-out; }


.scrollToTop span {
    margin-top: 6px; }


.showScrollTop {
    font-size: 14px;
    opacity: 1; }


a[data-lightbox] {
    position: relative;
    display: block;
    text-align: center; }


a[data-lightbox]:hover::before {
    content: "+";
    font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial;
    font-size: 32px;
    width: 50px;
    height: 50px;
    margin-left: -25px;
    border-radius: 50%;
    background: rgba(0, 0, 0, .6);
    color: #FFF;
    font-weight: 100;
    z-index: 1;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%); }


a[data-lightbox]:hover img {
    opacity: 0.6;
    -webkit-animation-fill-mode: none;
    animation-fill-mode: none; }


#lightbox-modal {
    display: flex;
    align-items: center; }


#lightbox-modal .modal-dialog {
    width: 90%;
    max-width: 900px;
    margin: 0 auto 0; }


#lightbox-modal .modal-dialog img {
    max-height: 90vh;
    margin: 0 auto; }


.lightbox-caption {
    padding: 20px;
    color: #FFF;
    background: rgba(0, 0, 0, .5);
    position: absolute;
    left: 15px;
    right: 15px;
    bottom: 5px; }


.close-lightbox {
    color: #FFF;
    font-size: 30px;
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 20;
    background: rgba(0, 0, 0, .5);
    border: none;
    line-height: 30px;
    padding: 0 9px 5px;
    opacity: 0.3; }


.close-lightbox:hover,
.next-lightbox:hover,
.prev-lightbox:hover {
    opacity: 1.0;
    color: #FFF; }


.next-lightbox,
.prev-lightbox {
    font-size: 20px;
    color: rgba(255, 255, 255, .9);
    background: rgba(0, 0, 0, .5);
    transition: all .2s ease-in-out;
    position: absolute;
    top: 45%;
    z-index: 1;
    opacity: 0.4; }


.next-lightbox {
    padding: 6px 8px 1px 13px;
    right: 25px; }


.prev-lightbox {
    padding: 6px 13px 1px 10px;
    left: 25px; }


.snapshot-lb .modal-body {
    padding-bottom: 45px; }


.snapshot-lb .lightbox-caption {
    padding: 0;
    color: rgba(0, 0, 0, .5);
    background: none; }


strong,
h2,
h4,
h5,
h6,
label,
.btn {
    font-family: "helvetica";
    font-weight: 400;
    color: $color-dark; }


.container {
    max-width: 1000px; }


.hero-bloc-text-sub {
    font-size: 36px; }


.statement-bloc-text {
    line-height: 26px;
    font-style: italic;
    font-size: 20px;
    text-align: center;
    font-weight: lighter;
    max-width: 520px;
    margin: auto auto auto auto; }


p {
    font-size: 11px; }


.tight-width-whitespace {
    max-width: 600px;
    margin: auto auto auto auto; }


h1 {
    color: $color-secondary;
    font-size: 32px;
    font-family: "helvetica";
    font-weight: 400;
    text-align: center; }


.cta-hero {
    margin-top: 22px;
    color: $color-primary !important;
    text-transform: uppercase;
    padding: 12px 28px 12px 28px;
    font-size: 16px;
    font-weight: 700; }


.social {
    max-width: 400px;
    margin: auto auto auto auto;
    color: rgba(255, 255, 255, .6) !important;
    transition: all .01s ease-in-out; }

.social:hover, .social:focus {
    color: rgba(255, 255, 255, .99) !important; }


strong {
    font-size: 22px;
    line-height: 1.4em;
    color: $color-secondary; }


h2 {
    font-size: 15px !important;
    text-transform: uppercase;
    font-weight: 700;
    color: #333333!important; }


.med-width-whitespace {
    max-width: 800px;
    margin: auto auto auto auto;
    box-shadow: 0px 0px 0px #000000; }

h4 {
    color: #333333!important; }


.icons {
    margin-bottom: 14px;
    margin-top: 24px; }

.portfolio-thumb {
    margin-bottom: 24px; }


.portfolio-row {
    margin-bottom: 44px;
    margin-top: 50px; }


.avatar {
    box-shadow: 0px 4px 9px rgba(0, 0, 0, 0.3); }


.black-background {
    background-color: rgba(165, 147, 99, 0.7);
    padding: 40px 40px 40px 40px; }


.objectifs a {
    color: $color-primary;
    font-weight: 900;
    text-decoration: underline!important;
    font-size: 11px; }

.objectifs a:hover, .objectifs a:focus-visible {
    transition: all .3s ease-in-out;
    outline: none!important;
    color: rgba(255, 255, 255, .6); }

.objectifs p {
    color: $color-primary !important; }

.objectifs__frame {
    margin-bottom: 10px; }


.realisations__title {
    font-size: 22px;
    padding: 0 267px;
    color: rgba(0, 0, 0, 0.6);
    margin: 18px 0; }

.btn-atomic-tangerine {
    background: #F3976C;
    color: #F3976C; }


.btn-atomic-tangerine:hover, .btn-atomic-tangerine:focus {
    background: #c27956 !important;
    color: #F3976C !important; }


.ltc-white {
    color: $color-primary !important; }


.ltc-white:hover, .ltc-white:focus {
    color: #cccccc!important; }


.ltc-atomic-tangerine {
    color: $color-secondary !important; }


.ltc-atomic-tangerine:hover, .ltc-atomic-tangerine:focus {
    color: #c27956!important; }


.bg-dots-bg {
    background-image: url("../img/dots-bg.png"); }


.bg-lines-h2-bg {
    background-image: url("../img/lines-h2-bg.png"); }


@media (max-width: 1024px) {
    .bloc {
        padding-left: 20px;
        padding-right: 20px; }

    .bloc.full-width-bloc,
    .bloc-tile-2.full-width-bloc .container,
    .bloc-tile-3.full-width-bloc .container,
    .bloc-tile-4.full-width-bloc .container {
        padding-left: 0;
        padding-right: 0; } }



@media (max-width: 992px) and (min-width: 768px) {
    .header__nav .header__nav--menu {
        max-width: 80%; }

    .nav-center.header__nav .header__nav--menu {
        max-width: 100%; } }



// @media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: landscape)


// @media only screen and (min-device-width: 1024px) and (max-device-width: 1366px) and (-webkit-min-device-pixel-ratio: 1.5)



@media (max-width: 991px) {
    .container {
        width: 100%; }

    .page-container,
    #hero-bloc {
        overflow-x: hidden;
        position: relative; }

    .bloc {
        padding-left: constant(safe-area-inset-left);
        padding-right: constant(safe-area-inset-right); }

    .bloc-group,
    .bloc-group .bloc {
        display: block;
        width: 100%; }

    .bloc-fill-screen .fill-bloc-top-edge,
    .bloc-fill-screen .fill-bloc-bottom-edge {
        padding: 0 20px;
        padding-left: constant(safe-area-inset-left);
        padding-right: constant(safe-area-inset-right); } }



@media (max-width: 767px) {
    .page-container {
        overflow-x: hidden;
        position: relative; }

    h1,
    strong,
    h2,
    h4,
    h5,
    h6,
    p,
    #disqus_thread {
        padding-left: 10px!important;
        padding-right: 10px!important; }

    .header__nav .header__nav--menu {
        padding-top: 0;
        border-top: 1px solid rgba(0, 0, 0, .2);
        float: none!important; }

    .navbar.row {
        margin-left: 0;
        margin-right: 0; }

    .header__nav--menu {
        position: inherit;
        transform: none;
        -webkit-transform: none;
        -ms-transform: none; }

    .header__nav--menu > li {
        margin-top: 0;
        border-bottom: 1px solid rgba(0, 0, 0, .1);
        background: rgba(0, 0, 0, .05);
        text-align: left;
        padding-left: 15px;
        width: 100%; }

    .header__nav--menu > li:hover {
        background: rgba(0, 0, 0, .08); }

    .dropdown .dropdown a .caret {
        float: none;
        margin: 5px 0 0 10px!important;
        border: 4px solid black;
        border-bottom-color: transparent;
        border-right-color: transparent;
        border-left-color: transparent; }

    #hero-bloc .header__nav .header__nav--menu {
        background: rgba(0, 0, 0, .8); }

    #hero-bloc .header__nav .header__nav--menu a {
        color: rgba(255, 255, 255, .6); }

    .hero {
        padding: 50px 0; }

    .hero-nav {
        left: -1px;
        right: -1px; }


    .header__nav--logo img {
        max-height: 40px;
        width: auto; }

    .nav-invert {
        float: none;
        width: 100%; }

    .nav-invert .navbar-toggle {
        float: left;
        margin-left: 10px; }

    .bloc {
        padding-left: 0;
        padding-right: 0; }

    .bloc-xxl,
    .bloc-xl,
    .bloc-lg {
        padding: 40px 0; }

    .bloc-sm,
    .bloc-md {
        padding-left: 0;
        padding-right: 0; }

    .bloc-tile-2 .container,
    .bloc-tile-3 .container,
    .bloc-tile-4 .container,
    .bloc-fill-screen .fill-bloc-top-edge,
    .bloc-fill-screen .fill-bloc-bottom-edge {
        padding-left: 0;
        padding-right: 0; }

    .a-block {
        padding: 0 10px; }

    .btn-dwn {
        display: none; }

    .voffset {
        margin-top: 5px; }

    .voffset-md {
        margin-top: 20px; }

    .voffset-lg {
        margin-top: 30px; }

    form {
        padding: 5px; }

    .close-lightbox {
        display: inline-block; }

    .blocsapp-device-iphone5 {
        background-size: 216px 425px;
        padding-top: 60px;
        width: 216px;
        height: 425px; }

    .blocsapp-device-iphone5 img {
        width: 180px;
        height: 320px; } }



@media (max-width: 400px) {
    .bloc {
        padding-left: 0;
        padding-right: 0; } }



@media (max-width: 767px) {
    .bloc-mob-center-text {
        text-align: center; } }

